.header {
  background-color: transparent;
  background-image: $black-pearl-to-bottom;
  padding: 30px 0 35px 0;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include vp-1023 {
    padding: 20px 0 25px 0;
  }

  @include vp-767 {
    padding: 10px 0 15px 0;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include vp-1023 {
    align-items: flex-start;
  }
}

.header__logo-link {
  display: block;
  margin-right: 4.91%;

  @include vp-767 {
    margin-top: 5px;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.header__logo-img {
  display: block;

  @include vp-767 {
    width: 35px;
    height: 35px;
  }
}

.header__contact-list {
  margin: 0;
  padding: 0;
  margin-bottom: 7px;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 39.94%;
  margin-right: 3.1%;

  @include vp-1023 {
    margin-right: 0;
    margin-bottom: 0;
    width: fit-content;
    max-width: 50%;
  }
}

.header__contact-item {
  &:last-of-type {
    margin-right: 0;

    @include vp-1023 {
      margin-right: 20px;
    }

    @include vp-767 {
      margin-right: 10px;
    }
  }

  &:not(:last-of-type) {
    margin-right: 20px;

    @include vp-1023 {
      margin-right: 20px;
    }
  }
}

.header__contact-link {
  display: block;
  background-repeat: no-repeat;
  background-position: left top 3px;
  background-size: 20px 20px;

  &--mail {
    background-image: url("../img/svg/mail.svg");
  }

  &--phone {
    background-image: url("../img/svg/phone.svg");
    padding-right: 25px;
  }

  @include vp-1023 {
    padding: 20px 20px;
    background-position: center;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.header__contact-capture {
  color: $color-alice-blue;
  padding-left: 35px;
  font-family: inherit;

  @include vp-1023 {
    display: none;
  }
}

.header__button {
  display: block;
  margin-right: 4.91%;
  margin-bottom: 6px;

  @include vp-1023 {
    display: none;
  }
}
